import { CONTRACT_ADDRESS, SWAP_CONTRACT_ABI } from 'constants/misc'
import { useCallback } from 'react'

import { useContract } from './useContract'

export function useGetCycleWinners() {
  const contract = useContract(CONTRACT_ADDRESS, SWAP_CONTRACT_ABI)

  return useCallback(
    async (cycle: number) => {
      if (!contract) throw new Error('missing contract')
      try {
        const winners = await contract.cycleWinners(cycle)

        return winners
      } catch (error) {
        // Handle errors
        console.error(error)
        throw error
      }
    },
    [contract]
  )
}
